.footer {
	background-color: $white;
	padding: 55px 0;
	@include r(640) {
		text-align: center;
		padding: 59px 0 48px 0; }
	@include r(481) {
		padding: 30px 0; }
	.inner {
		padding: 0;
		@include r(1266) {
			padding: 0 25px; }
		@include r(640) {
			padding: 0 55px; }
		@include r(481) {
			padding: 0 10px; } }
	&__logo {
		float: right;
		@include r(640) {
			margin-top: 45px; }
		@include r(321) {
			float: none; }
		.icon-logo-meduzabrush {
			fill: $main; } }
	&__info {
		display: inline-block;
		vertical-align: top;
		margin-right: 65px;
		@include sm { //767
			margin-right: 30px; }
		@include r(667) {
			margin-right: 15px; }
		@include r(640) {
			margin-right: 0;
			display: block;
			margin-bottom: 44px;
			&:last-child {
				margin-bottom: 0; } }
		p {
			color: $gray-darker;
			@include font-family($gotham-light);
			font-size: 14px;
			line-height: 2.13;
			a {
				text-decoration: none;
				color: $gray-darker; } }
		a {
			strong {
				@include font-family($gotham-md); } }
		&-wrapper {
			@include clr;
			display: inline-block;
			@include r(640) {
				float: left;
				text-align: left; }
			@include r(321) {
				text-align: center;
				float: none; } } } }

