body {
	background: #FFF;
	// +css-lock(16, 50, 600, 1000)
	line-height: 1.4; }

.slider {
	@include clr;
	font-size: 0;
	position: relative; }

.item {
	&-slider {
		overflow: hidden;
		display: inline-block;
		vertical-align: top;
		&:hover a {
			transform: scale(1.1);
			@include r(768) {
				transform: scale(1); } }
		a {
			@include cover;
			text-decoration: none;
			transition: all .24s ease-in-out;
			cursor: pointer;
			display: block; }
		// item sizes
		&.item {
			&-lg {
				width: 65.5%;
				@include r(640) {
					height: 630px; }
				@include r(570) {
					height: auto; }
				a {
					padding-bottom: 50.56%;
					@include r(640) {
						padding-bottom: 100%; } } }
			&-md {
				width: 34.50%;
				@include r(640) {
					height: 630px; }
				@include r(570) {
					height: auto; }
				a {
					padding-bottom: 94.58%;
					@include r(640) {
						padding-bottom: 100%; } } }
			&-sm {
				width: 33.3333%;
				@include r(640) {
					height: 630px; }
				@include r(570) {
					height: auto; }
				a {
					padding-bottom: 100%; } } } }
	&-slider img {
		max-width: 100%;
		height: auto; } }

.slick {
	&-dots {
		text-align: center;
		position: absolute;
		bottom: 40px;
		left: 50%;
		transform: translateX(-50%);
		@include r(570) {
			bottom: 20px; }
		li {
			display: inline-block;
			vertical-align: middle;
			line-height: 15px;
			padding: 0 10px;
			button {
				background-color: $coffee;
				font-size: 0;
				width: 9px;
				vertical-align: middle;
				height: 9px;
				transition: all .24s ease-in-out;
				border-radius: 50px; }
			&.slick-active {
				button {
					background-color: rgba($coffee, .50);
					transform: scale(1.70); } } } } }
