body {
  background: #FFF;
  line-height: 1.4;
}

.slider {
  font-size: 0;
  position: relative;
}

.slider:after {
  content: " ";
  display: table;
  clear: both;
}

.item-slider {
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
}

.item-slider:hover a {
  transform: scale(1.1);
}

@media only screen and (max-width: 768px) {
  .item-slider:hover a {
    transform: scale(1);
  }
}

.item-slider a {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  text-decoration: none;
  transition: all .24s ease-in-out;
  cursor: pointer;
  display: block;
}

.item-slider.item-lg {
  width: 65.5%;
}

@media only screen and (max-width: 640px) {
  .item-slider.item-lg {
    height: 630px;
  }
}

@media only screen and (max-width: 570px) {
  .item-slider.item-lg {
    height: auto;
  }
}

.item-slider.item-lg a {
  padding-bottom: 50.56%;
}

@media only screen and (max-width: 640px) {
  .item-slider.item-lg a {
    padding-bottom: 100%;
  }
}

.item-slider.item-md {
  width: 34.50%;
}

@media only screen and (max-width: 640px) {
  .item-slider.item-md {
    height: 630px;
  }
}

@media only screen and (max-width: 570px) {
  .item-slider.item-md {
    height: auto;
  }
}

.item-slider.item-md a {
  padding-bottom: 94.58%;
}

@media only screen and (max-width: 640px) {
  .item-slider.item-md a {
    padding-bottom: 100%;
  }
}

.item-slider.item-sm {
  width: 33.3333%;
}

@media only screen and (max-width: 640px) {
  .item-slider.item-sm {
    height: 630px;
  }
}

@media only screen and (max-width: 570px) {
  .item-slider.item-sm {
    height: auto;
  }
}

.item-slider.item-sm a {
  padding-bottom: 100%;
}

.item-slider img {
  max-width: 100%;
  height: auto;
}

.slick-dots {
  text-align: center;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}

@media only screen and (max-width: 570px) {
  .slick-dots {
    bottom: 20px;
  }
}

.slick-dots li {
  display: inline-block;
  vertical-align: middle;
  line-height: 15px;
  padding: 0 10px;
}

.slick-dots li button {
  background-color: #c8b29a;
  font-size: 0;
  width: 9px;
  vertical-align: middle;
  height: 9px;
  transition: all .24s ease-in-out;
  border-radius: 50px;
}

.slick-dots li.slick-active button {
  background-color: rgba(200, 178, 154, 0.5);
  transform: scale(1.7);
}

.inner {
  margin: 0 auto;
  padding: 0 50px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1270px) {
