}

.menu {
  font-size: 0;
}

.menu__item {
  display: inline-block;
  vertical-align: middle;
  margin: 0 25px;
}

@media only screen and (max-width: 992px) {
  .menu__item {
    margin: 0 15px;
  }
}

.menu__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 12px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding: 19px 0;
  line-height: 1;
  font-family: "proximanova-bold", Arial, sans-serif;
}

.menu__link:after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  opacity: 0;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  width: 0;
  height: 1px;
  transition: all .24s ease-in-out;
  background-color: rgba(200, 178, 154, 0.3);
  -webkit-transition: all .24s ease-in-out;
}

.menu__link.is-current:after {
  display: block;
  background-color: #c8b29a;
  opacity: 1;
  width: 100%;
}

.menu__link:hover:after {
  opacity: 1;
  width: 100%;
}

.menu.header_menu {
  margin-left: -25px;
}

@media only screen and (max-width: 992px) {
  .menu.header_menu {
    margin-left: -15px;
  }
}

