.btn-ghost {
  border-color: inherit;
  background-color: transparent;
  position: relative;
}

.btn-ghost:after, .btn-ghost:before {
  content: "";
  display: block;
  position: absolute;
  background-color: transparent;
}

.btn-ghost:after {
  right: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: #958779;
}

.btn-ghost:before {
  background-color: #958779;
  width: 100%;
  left: 0;
  top: 100%;
}

.btn-nav {
  display: none;
  position: absolute;
  top: 0;
  font-size: 0;
  outline: none;
  left: 0;
  background-color: transparent;
  background-image: none;
  width: 30px;
  height: 30px;
}

.btn-nav:hover span {
  background-color: transparent;
}

.btn-nav span {
  position: absolute;
  -webkit-transition: all .24s ease-in-out;
  transition: all .24s ease-in-out;
  background-color: #958779;
  width: 100%;
  height: 4px;
  top: 0;
  left: 0;
  border-radius: 4px;
}

.btn-nav span:after, .btn-nav span:before {
  content: '';
  display: block;
  border-radius: 4px;
  position: absolute;
  background-color: #958779;
  -webkit-transition: all .24s ease-in-out;
  transition: all .24s ease-in-out;
  width: 100%;
  height: 100%;
}

.btn-nav span:after {
  top: 13px;
}

.btn-nav span:before {
  top: 26px;
}

@media only screen and (max-width: 768px) {
  .btn-nav {
    display: block;
  }
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
